body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* { /* Sets font for app, used in buttons */
    font-family: "Open Sans", monospace;
    font-size: 1em;
  }
  
  /* Aligns the bars in the top center of page */
  .array-container {
    align-items: top;
    justify-content: center;
    display: flex;
    margin-top: 0 auto;
  }
  
  /* Sets color of array bar and distance in between */
  .array-bar {
    background-color: lightsteelblue;
    margin: 0.2%;
  }
  
  /* Sets up footer with gray color */
  .app-footer {
    display: flex;
    overflow-x: auto;
    background-color: rgb(45, 48, 52);
    margin-top: auto;
  }
  
  /* More footer set up */
  .app-footer ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    white-space: nowrap;
  }
  
  /* Sets up round buttons for the app with white test and transparent background */
  .app-button {
    border-radius: 0.5em;
    border-width: 0.05em;
    color: white;
    padding: 0.5em;
    margin: 0 0.5em;
    background-color: transparent;
  }
  
  /* Makes no outline for buttons */
  .app-button:focus {
    outline: none;
  }
  
  /* Makes button white when cursor is over it */
  .app-button:hover {
    color: rgb(45, 48, 52);
    background-color: #eaeaea;
  }
  
  /* Sets up visualizer container */
  .visualizer-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Set up for the footer */
  @media (min-width: 700px) {
    .app-footer {
      justify-content: center;
    }
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

