* { /* Sets font for app, used in buttons */
    font-family: "Open Sans", monospace;
    font-size: 1em;
  }
  
  /* Aligns the bars in the top center of page */
  .array-container {
    align-items: top;
    justify-content: center;
    display: flex;
    margin-top: 0 auto;
  }
  
  /* Sets color of array bar and distance in between */
  .array-bar {
    background-color: lightsteelblue;
    margin: 0.2%;
  }
  
  /* Sets up footer with gray color */
  .app-footer {
    display: flex;
    overflow-x: auto;
    background-color: rgb(45, 48, 52);
    margin-top: auto;
  }
  
  /* More footer set up */
  .app-footer ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    white-space: nowrap;
  }
  
  /* Sets up round buttons for the app with white test and transparent background */
  .app-button {
    border-radius: 0.5em;
    border-width: 0.05em;
    color: white;
    padding: 0.5em;
    margin: 0 0.5em;
    background-color: transparent;
  }
  
  /* Makes no outline for buttons */
  .app-button:focus {
    outline: none;
  }
  
  /* Makes button white when cursor is over it */
  .app-button:hover {
    color: rgb(45, 48, 52);
    background-color: #eaeaea;
  }
  
  /* Sets up visualizer container */
  .visualizer-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Set up for the footer */
  @media (min-width: 700px) {
    .app-footer {
      justify-content: center;
    }
  }